import { Button, Form, Input, Modal } from 'antd'
import { TaskTypeSelect } from 'components/task-type-select'
import { UserSelect } from 'components/user-select'
import React, { useEffect } from 'react'
import { useDeleteTask, useEditTask } from 'utils/task'
import { useTasksModal, useTasksQueryKey } from './util'

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 }
}

export const TaskModal = () => {
  const [form] = Form.useForm()
  const { editingTask, editingTaskId, close } = useTasksModal()
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(
    useTasksQueryKey()
  )
  const { mutate: deleteTask } = useDeleteTask(useTasksQueryKey())

  const onCancel = () => {
    close()
    form.resetFields()
  }
  const onOk = async () => {
    await editTask({ ...editingTask, ...form.getFieldsValue() })
    close()
  }

  const startDelete = () => {
    close()
    Modal.confirm({
      okText: 'confirm',
      cancelText: 'cancel',
      title: 'Confirm delete Task ?',
      onOk() {
        return deleteTask({ id: Number(editingTaskId) })
      }
    })
  }

  useEffect(() => {
    form.setFieldsValue(editingTask)
  }, [editingTask, form])

  return (
    <Modal
      forceRender={true}
      onOk={onOk}
      onCancel={onCancel}
      okText={'Confirm'}
      cancelText={'Cancel'}
      confirmLoading={editLoading}
      title={'Editing Task'}
      visible={!!editingTaskId}
    >
      <Form {...layout} initialValues={editingTask} form={form}>
        <Form.Item
          label={'Task Name'}
          name={'name'}
          rules={[{ required: true, message: 'Input task name' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label={'Processor'} name={'processorId'}>
          <UserSelect defaultOptionName={'Processor'} />
        </Form.Item>
        <Form.Item label={'Type'} name={'typeId'}>
          <TaskTypeSelect />
        </Form.Item>
      </Form>
      <div style={{ textAlign: 'right' }}>
        <Button
          onClick={startDelete}
          style={{ fontSize: '14px' }}
          size={'small'}
        >
          delete
        </Button>
      </div>
    </Modal>
  )
}
