import { Button, Input } from 'antd'
import { Row } from 'components/lib'
import { TaskTypeSelect } from 'components/task-type-select'
import { UserSelect } from 'components/user-select'
import React from 'react'
import { useSetUrlSearchParam } from 'utils/url'
import { useTasksSearchParams } from './util'

export const SearchPanel = () => {
  const searchParams = useTasksSearchParams()
  const setSearchParam = useSetUrlSearchParam()
  const reset = () => {
    setSearchParam({
      typeId: undefined,
      processorId: undefined,
      tagId: undefined,
      name: undefined
    })
  }

  return (
    <Row marginBottom={4} gap={true}>
      <Input
        style={{ width: '20rem' }}
        placeholder={'task name'}
        value={searchParams.name}
        onChange={evt => setSearchParam({ name: evt.target.value })}
      />
      <UserSelect
        defaultOptionName={'processor'}
        value={searchParams.processorId}
        onChange={value => setSearchParam({ processorId: value })}
      />
      <TaskTypeSelect
        defaultOptionName={'task type'}
        value={searchParams.typeId}
        onChange={value => setSearchParam({ typeId: value })}
      />
      <Button type={'link'} onClick={reset}>
        Clear Filter
      </Button>
    </Row>
  )
}
