import { SearchPanel } from './search-panel'
import { List } from './list'
import React from 'react'
import { useDebounce, useDocumentTitle } from 'utils'
import { useProjects } from 'utils/project'
import { useUsers } from 'utils/user'
import { useProjectModal, useProjectsSearchParams } from './utils'
import { ButtonNoPadding, ErrorBox, Row, ScreenContainer } from 'components/lib'

export const ProjectListScreen = () => {
  useDocumentTitle('项目列表', false)

  // 直接使用 const [param] = useUrlQueryParam(['name', 'personId']) 可能会出现循环渲染问题
  // 一种原因是useUrlQueryParam返回的值，使得param不断更新
  // 另一种是useUrlQueryParam的参数，若useUrlQueryParam方法内部添加了useUrlQueryParam参数作为依赖，会导致useUrlQueryParam内部不断更新
  // 使用此方法解决第二种问题 const [keys] = useState<('name' | 'personId')[]>(['name', 'personId'])

  const [param, setParam] = useProjectsSearchParams()
  const { error, isLoading, data: list } = useProjects(useDebounce(param, 200))
  const { data: users } = useUsers()
  const { open } = useProjectModal()

  return (
    <ScreenContainer>
      <Row between={true}>
        <h1>Projects</h1>
        <ButtonNoPadding type={'link'} onClick={open}>
          Create Project
        </ButtonNoPadding>
      </Row>
      <SearchPanel
        users={users || []}
        param={param}
        setParam={setParam}
      ></SearchPanel>
      <ErrorBox error={error} />
      <List
        users={users || []}
        dataSource={list || []}
        loading={isLoading}
      ></List>
    </ScreenContainer>
  )
}

// 为什么会渲染
ProjectListScreen.whyDidYouRender = true

