import { Button, List, Modal } from 'antd'
import { Row, ScreenContainer } from 'components/lib'
import dayjs from 'dayjs'
import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { useProjectInUrl } from 'screens/Kanban/util'
import { Epic } from 'types/epic'
import { useDeleteEpic, useEpics } from 'utils/epic'
import { useTasks } from 'utils/task'
import { CreateEpic } from './create-epic'
import { useEpicSearchParams, useEpicsQueryKey } from './util'

export const EpicScreen = () => {
  const { data: currentProject } = useProjectInUrl()
  const { data: epics } = useEpics(useEpicSearchParams())
  const { data: tasks } = useTasks({ projectId: currentProject?.id })
  const { mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey())
  const [epicCreateOpen, setEpicCreateOpen] = useState(false)
  const confirmDeleteEpic = (epic: Epic) => {
    Modal.confirm({
      title: `Delete tip`,
      content: `Sure to DELETE ${epic.name}`,
      okText: 'confirm',
      cancelText: 'cancel',
      onOk() {
        deleteEpic({ id: epic.id })
      }
    })
  }
  return (
    <ScreenContainer>
      <Row between={true}>
        <h1>Epic {currentProject?.name}</h1>
        <Button onClick={() => setEpicCreateOpen(true)} type={'link'}>
          Create Epic
        </Button>
      </Row>
      <List
        style={{ overflow: 'scroll' }}
        dataSource={epics}
        itemLayout={'vertical'}
        renderItem={epic => (
          <List.Item>
            <List.Item.Meta
              title={
                <Row between={true}>
                  <span>{epic.name}</span>
                  <Button
                    onClick={() => {
                      confirmDeleteEpic(epic)
                    }}
                    type={'link'}
                  >
                    delete
                  </Button>
                </Row>
              }
              description={
                <div>
                  <div>Start: {dayjs(epic.start).format('YYYY-MM-DD')}</div>
                  <div>End: {dayjs(epic.end).format('YYYY-MM-DD')}</div>
                </div>
              }
            />
            <div>
              {tasks
                ?.filter(task => task.epicId === epic.id)
                .map(task => (
                  <Link
                    to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                    key={task.id}
                  >
                    {task.name}
                  </Link>
                ))}
            </div>
          </List.Item>
        )}
      />
      <CreateEpic
        onClose={() => setEpicCreateOpen(false)}
        visible={epicCreateOpen}
      />
    </ScreenContainer>
  )
}
