import styled from '@emotion/styled'
import { Button, Drawer, Form, Input, Spin } from 'antd'
import { ErrorBox } from 'components/lib'
import { UserSelect } from 'components/user-select'
import React, { useEffect } from 'react'
import { useAddProject, useEditProject } from 'utils/project'
import { useProjectModal, useProjectQueryKey } from './utils'

export const ProjectModal = () => {
  const { projectModalOpen, close, editingProject, isLoading } =
    useProjectModal()
  const useMutateProject = editingProject ? useEditProject : useAddProject

  const {
    mutateAsync,
    error,
    isLoading: mutateLoading
  } = useMutateProject(useProjectQueryKey())
  const [form] = Form.useForm()
  const onFinish = (values: any) => {
    mutateAsync({ ...editingProject, ...values }).then(() => {
      form.resetFields()
      close()
    })
  }
  const closeModal = () => {
    form.resetFields()
    close()
  }
  const title = editingProject ? 'Edit' : 'Create'

  useEffect(() => {
    form.setFieldsValue(editingProject)
  }, [editingProject, form])

  return (
    <Drawer
      forceRender={true}
      onClose={closeModal}
      visible={projectModalOpen}
      width={'100%'}
    >
      <Container>
        {isLoading ? (
          <Spin size={'large'} />
        ) : (
          <>
            <h1>{title}</h1>
            <ErrorBox error={error} />
            <Form
              form={form}
              layout={'vertical'}
              style={{ width: '40rem' }}
              onFinish={onFinish}
            >
              <Form.Item
                label={'Name'}
                name={'name'}
                rules={[{ required: true, message: 'input project name' }]}
              >
                <Input placeholder={'project name'}></Input>
              </Form.Item>
              <Form.Item
                label={'Org'}
                name={'organization'}
                rules={[{ required: true, message: 'input organization' }]}
              >
                <Input placeholder={'organization'}></Input>
              </Form.Item>
              <Form.Item label={'Principal'} name={'personId'}>
                <UserSelect defaultOptionName={'负责人'} />
              </Form.Item>
              <Form.Item style={{ textAlign: 'center' }}>
                <Button
                  loading={mutateLoading}
                  type={'primary'}
                  htmlType={'submit'}
                >
                  Submit
                </Button>
              </Form.Item>
            </Form>
          </>
        )}
      </Container>
    </Drawer>
  )
}

const Container = styled.div`
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`
